import React, { useState, useEffect, useRef } from 'react';
import { useLocation } from 'react-router-dom';
import { Button } from '../components/ui/button';
import AIChat, { Message } from '../components/features/ai-chat/ai-chat';
import CommandPalette from '../components/features/command/command-palette';
import { usePlanStore } from '../lib/store';
import { toast } from 'sonner';
import { Plan } from '../types/task';
import ReactMarkdown from 'react-markdown';

// 文件内容类型定义
interface FileContents {
  [key: string]: string;
}

const WorkspacePage: React.FC = () => {
  const location = useLocation();
  const { plans } = usePlanStore();
  const [currentPlan, setCurrentPlan] = useState<Plan | null>(null);
  
  const [isLeftSidebarCollapsed, setIsLeftSidebarCollapsed] = useState(false);
  const [leftSidebarWidth, setLeftSidebarWidth] = useState(256); // Default width of 64 in rem (w-64)
  const [isLeftResizing, setIsLeftResizing] = useState(false);
  const leftResizingRef = useRef<{ startX: number; startWidth: number } | null>(null);
  
  const [isRightSidebarCollapsed, setIsRightSidebarCollapsed] = useState(false);
  const [rightSidebarWidth, setRightSidebarWidth] = useState(384); // Default width of 96 in rem (w-96)
  const [isResizing, setIsResizing] = useState(false);
  const resizingRef = useRef<{ startX: number; startWidth: number } | null>(null);
  
  const [isCommandPaletteOpen, setIsCommandPaletteOpen] = useState(false);
  const [openFiles, setOpenFiles] = useState<string[]>([]);
  const [currentFile, setCurrentFile] = useState<string>('');
  const [fileContents, setFileContents] = useState<FileContents>({});

  // 从URL获取planId并加载相应的计划
  useEffect(() => {
    const searchParams = new URLSearchParams(location.search);
    const planId = searchParams.get('planId');
    
    if (planId && plans) {
      const plan = plans.find(p => p.id === planId);
      if (plan) {
        setCurrentPlan(plan);
        
        // 生成计划相关的文件内容
        const planFiles: FileContents = {
          'plan-overview.md': generatePlanOverviewContent(plan),
          'tasks.md': generateTasksContent(plan),
          'progress.md': generateProgressContent(plan),
          'notes.md': ''
        };
        
        setFileContents(prev => ({...prev, ...planFiles}));
        
        // 设置默认打开的文件
        setOpenFiles(Object.keys(planFiles));
        setCurrentFile('plan-overview.md');
      }
    } else {
      // 没有计划ID时显示默认工作区
      const defaultContent = {
        'workspace.txt': `# 任务计划 AI 工作区

这是一个集成式AI工作环境，你可以：
- 向AI提出关于任务的问题
- 获取关于任务拆解的建议
- 使用AI协助管理和优化你的任务计划

## 使用方法

1. 在右侧AI助手中提问
2. 使用⌘K打开命令面板
3. 使用@符号引用文件和资源`
      };
      
      setFileContents(prev => ({...prev, ...defaultContent}));
      setOpenFiles(['workspace.txt']);
      setCurrentFile('workspace.txt');
    }
  }, [location.search, plans]);

  // 生成计划概览内容
  const generatePlanOverviewContent = (plan: Plan): string => {
    return `# ${plan.title}

## 描述
${plan.description || '暂无描述'}

## 意义
${plan.significance || '暂无描述'}

## 创建时间
${plan.createdAt.toLocaleString()}

## 计划状态
${plan.status}

## 总进度
${plan.progress}%
`;
  };

  // 生成任务内容
  const generateTasksContent = (plan: Plan): string => {
    let content = `# ${plan.title} - 任务列表\n\n`;
    
    if (plan.stages && plan.stages.length > 0) {
      plan.stages.forEach(stage => {
        content += `## ${stage.title}\n\n`;
        
        if (stage.tasks && stage.tasks.length > 0) {
          stage.tasks.forEach(task => {
            content += `- [${task.completed ? 'x' : ' '}] ${task.title}\n`;
            if (task.description) {
              content += `  ${task.description}\n`;
            }
          });
        } else {
          content += `暂无任务\n`;
        }
        
        content += '\n';
      });
    } else {
      content += '暂无任务阶段\n';
    }
    
    return content;
  };

  // 生成进度内容
  const generateProgressContent = (plan: Plan): string => {
    return `# ${plan.title} - 进度跟踪

## 总体进度
${plan.progress}% 完成

## 当前阶段
${plan.stages.find(s => s.id === plan.currentStageId)?.title || '未设置'}

## 延迟情况
${plan.delayedDays ? `已延迟 ${plan.delayedDays} 天` : '按时进行中'}

## AI互动次数
${plan.aiInteractions || 0} 次

## 最近更新
${plan.updatedAt.toLocaleString()}
`;
  };

  // 定义可用命令
  const commands = [
    {
      id: 'new-file',
      name: '新建文件',
      description: '创建一个新的文件',
      icon: (
        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
        </svg>
      ),
      action: () => {
        const fileName = prompt('请输入文件名:');
        if (fileName && !openFiles.includes(fileName)) {
          setOpenFiles(prev => [...prev, fileName]);
          setFileContents(prev => ({
            ...prev,
            [fileName]: ''
          }));
          setCurrentFile(fileName);
          toast.success(`文件 ${fileName} 已创建`);
        }
      }
    },
    {
      id: 'toggle-left-sidebar',
      name: '切换左侧栏',
      description: '显示或隐藏左侧文件栏',
      icon: (
        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h7" />
        </svg>
      ),
      action: () => {
        setIsLeftSidebarCollapsed(!isLeftSidebarCollapsed);
      }
    },
    {
      id: 'toggle-right-sidebar',
      name: '切换AI助手',
      description: '显示或隐藏右侧AI助手',
      icon: (
        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 10h.01M12 10h.01M16 10h.01M9 16H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-5l-5 5v-5z" />
        </svg>
      ),
      action: () => {
        setIsRightSidebarCollapsed(!isRightSidebarCollapsed);
      }
    },
    {
      id: 'ask-ai',
      name: '询问AI',
      description: '直接向AI助手提问',
      icon: (
        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9.663 17h4.673M12 3v1m6.364 1.636l-.707.707M21 12h-1M4 12H3m3.343-5.657l-.707-.707m2.828 9.9a5 5 0 117.072 0l-.548.547A3.374 3.374 0 0014 18.469V19a2 2 0 11-4 0v-.531c0-.895-.356-1.754-.988-2.386l-.548-.547z" />
        </svg>
      ),
      action: () => {
        setIsRightSidebarCollapsed(false);
        toast.info('请在AI助手栏中提问');
      }
    },
    {
      id: 'go-to-dashboard',
      name: '返回任务计划',
      description: '返回到任务计划页面',
      icon: (
        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6" />
        </svg>
      ),
      action: () => {
        window.location.href = '/dashboard';
      }
    }
  ];

  // 处理AI消息
  const handleSendMessage = async (message: string, references?: string[]): Promise<string> => {
    // 模拟AI响应
    return new Promise((resolve) => {
      setTimeout(() => {
        let response = '';
        
        // 根据消息内容模拟不同的响应
        if (message.toLowerCase().includes('任务拆解')) {
          response = `我可以帮你拆解任务。一个好的任务拆解应该包含以下几个步骤：

1. 明确任务目标
2. 将大任务分解为小任务
3. 评估每个小任务的优先级
4. 设定每个小任务的完成时间
5. 分配资源

希望这对你有所帮助！`;
        } else if (message.toLowerCase().includes('优化')) {
          response = `要优化你的任务计划，可以考虑以下几点：

1. 使用番茄工作法提高专注度
2. 建立例行公事，减少决策疲劳
3. 批量处理同类型的任务
4. 定期回顾和调整你的计划
5. 使用自动化工具减少重复工作`;
        } else if (references && references.length > 0) {
          // 如果有引用文件，基于文件内容给出响应
          const fileContent = references.map(file => fileContents[file] || `(无法读取文件 ${file})`).join('\n\n');
          response = `我已分析了你引用的文件。以下是我的发现：

根据文件内容，这是一个使用React框架构建的任务管理应用。主要组件包括计划树(plan-tree)、工作区和仪表盘。

我建议你可以：
1. 检查组件之间的数据流是否清晰
2. 确保状态管理逻辑集中且一致
3. 考虑添加单元测试确保功能稳定性`;
        } else {
          response = `我是你的AI助手，可以帮助你:

- 回答关于任务管理的问题
- 提供任务拆解和优化建议
- 分析项目文件和代码

你可以尝试询问关于"任务拆解"或"优化"的问题，或者使用@引用特定文件，我会基于文件内容给你更具体的建议。`;
        }
        
        resolve(response);
      }, 1500);
    });
  };

  // 监听快捷键
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      // 检查Cmd+K或Ctrl+K
      if ((e.metaKey || e.ctrlKey) && e.key === 'k') {
        e.preventDefault();
        setIsCommandPaletteOpen(true);
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, []);

  // 处理左侧边栏拖拽调整宽度
  const handleLeftResizeMouseDown = (e: React.MouseEvent) => {
    e.preventDefault();
    setIsLeftResizing(true);
    leftResizingRef.current = {
      startX: e.clientX,
      startWidth: leftSidebarWidth
    };
  };

  useEffect(() => {
    const handleLeftResizeMouseMove = (e: MouseEvent) => {
      if (!isLeftResizing || !leftResizingRef.current) return;
      
      const delta = e.clientX - leftResizingRef.current.startX;
      const newWidth = Math.max(200, Math.min(500, leftResizingRef.current.startWidth + delta));
      setLeftSidebarWidth(newWidth);
    };

    const handleLeftResizeMouseUp = () => {
      setIsLeftResizing(false);
      leftResizingRef.current = null;
    };

    if (isLeftResizing) {
      document.addEventListener('mousemove', handleLeftResizeMouseMove);
      document.addEventListener('mouseup', handleLeftResizeMouseUp);
    }

    return () => {
      document.removeEventListener('mousemove', handleLeftResizeMouseMove);
      document.removeEventListener('mouseup', handleLeftResizeMouseUp);
    };
  }, [isLeftResizing]);

  // 处理右侧边栏拖拽调整宽度
  const handleResizeMouseDown = (e: React.MouseEvent) => {
    e.preventDefault();
    setIsResizing(true);
    resizingRef.current = {
      startX: e.clientX,
      startWidth: rightSidebarWidth
    };
  };

  useEffect(() => {
    const handleResizeMouseMove = (e: MouseEvent) => {
      if (!isResizing || !resizingRef.current) return;
      
      const delta = resizingRef.current.startX - e.clientX;
      const newWidth = Math.max(280, Math.min(800, resizingRef.current.startWidth + delta));
      setRightSidebarWidth(newWidth);
    };

    const handleResizeMouseUp = () => {
      setIsResizing(false);
      resizingRef.current = null;
    };

    if (isResizing) {
      document.addEventListener('mousemove', handleResizeMouseMove);
      document.addEventListener('mouseup', handleResizeMouseUp);
    }

    return () => {
      document.removeEventListener('mousemove', handleResizeMouseMove);
      document.removeEventListener('mouseup', handleResizeMouseUp);
    };
  }, [isResizing]);

  // 渲染文件内容的函数
  const renderFileContent = (content: string) => {
    if (!content) return <div className="p-4 text-gray-500">空文件</div>;
    
    // 对于markdown文件使用markdown渲染
    if (currentFile.endsWith('.md')) {
      return (
        <div className="p-4 prose prose-sm max-w-none">
          <ReactMarkdown>{content}</ReactMarkdown>
        </div>
      );
    }
    
    // 对于普通文本或代码文件使用预格式化文本
    return (
      <pre className="p-4 font-mono text-sm whitespace-pre-wrap">{content}</pre>
    );
  };

  return (
    <div className="h-screen w-screen flex overflow-hidden bg-gray-100">
      {/* 左侧文件浏览器 */}
      <aside 
        className={`bg-gray-800 text-white flex flex-col transition-all ${isLeftSidebarCollapsed ? 'w-12' : ''}`}
        style={{ width: isLeftSidebarCollapsed ? '3rem' : `${leftSidebarWidth}px` }}
      >
        <div className="flex items-center justify-between p-4 border-b border-gray-700">
          <h2 className={`font-medium ${isLeftSidebarCollapsed ? 'hidden' : 'block'}`}>文件浏览器</h2>
          <button 
            onClick={() => setIsLeftSidebarCollapsed(!isLeftSidebarCollapsed)}
            className="text-gray-400 hover:text-white"
          >
            {isLeftSidebarCollapsed ? (
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 5l7 7-7 7M5 5l7 7-7 7" />
              </svg>
            ) : (
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 19l-7-7 7-7m8 14l-7-7 7-7" />
              </svg>
            )}
          </button>
        </div>
        
        {/* 文件树组件 */}
        <div className="flex-1 overflow-auto p-2">
          {!isLeftSidebarCollapsed && (
            <div className="py-2">
              {Object.keys(fileContents).map(fileName => (
                <div 
                  key={fileName}
                  className={`flex items-center ${fileName === currentFile ? 'text-white bg-gray-700' : 'text-gray-300 hover:text-white'} py-1 px-2 rounded hover:bg-gray-700 cursor-pointer`}
                  onClick={() => {
                    if (!openFiles.includes(fileName)) {
                      setOpenFiles(prev => [...prev, fileName]);
                    }
                    setCurrentFile(fileName);
                  }}
                >
                  <svg className="w-5 h-5 mr-2 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
                  </svg>
                  <span className="truncate">{fileName}</span>
                </div>
              ))}
            </div>
          )}
        </div>
      </aside>

      {/* 中间编辑器区域 */}
      <main className="flex-1 flex flex-col h-full overflow-auto">
        {/* 文件标签栏 */}
        <div className="bg-gray-700 text-white h-12 flex items-center px-2 space-x-1 overflow-x-auto">
          {openFiles.map(file => (
            <button
              key={file}
              className={`px-3 py-1 rounded-t-md text-sm flex items-center space-x-2 ${
                currentFile === file ? 'bg-gray-100 text-gray-800' : 'bg-gray-600 text-gray-200 hover:bg-gray-500'
              }`}
              onClick={() => setCurrentFile(file)}
            >
              <span>{file}</span>
              {currentFile === file && (
                <span
                  className="ml-2 text-gray-500 hover:text-gray-700"
                  onClick={(e) => {
                    e.stopPropagation();
                    // 关闭文件标签的逻辑
                    setOpenFiles(prev => prev.filter(f => f !== file));
                    if (currentFile === file) {
                      // 如果关闭的是当前文件，选择下一个文件
                      const index = openFiles.indexOf(file);
                      if (index > 0) {
                        setCurrentFile(openFiles[index - 1]);
                      } else if (openFiles.length > 1) {
                        setCurrentFile(openFiles[1]);
                      }
                    }
                  }}
                >
                  ×
                </span>
              )}
            </button>
          ))}
        </div>

        {/* 文件内容区域 */}
        <div className="flex-1 overflow-auto bg-white">
          {currentFile && fileContents[currentFile] ? (
            renderFileContent(fileContents[currentFile])
          ) : (
            <div className="flex items-center justify-center h-full text-gray-400">
              请选择或创建一个文件
            </div>
          )}
        </div>
      </main>

      {/* AI助手侧边栏 */}
      <aside
        className={`bg-gray-50 border-l border-gray-200 flex flex-col ${isRightSidebarCollapsed ? 'w-0' : ''}`}
        style={{ width: isRightSidebarCollapsed ? '0' : `${rightSidebarWidth}px` }}
      >
        {!isRightSidebarCollapsed && (
          <>
            <div className="h-12 border-b border-gray-200 flex items-center justify-between px-4">
              <h2 className="font-medium text-gray-800">AI 助手</h2>
              <button
                className="text-gray-500 hover:text-gray-700"
                onClick={() => setIsRightSidebarCollapsed(true)}
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
            <div className="flex-1 overflow-hidden">
              <AIChat 
                onSendMessage={handleSendMessage} 
                availableFiles={openFiles}
              />
            </div>
          </>
        )}
        {/* 拖拽区域 */}
        {!isRightSidebarCollapsed && (
          <div
            className="absolute top-0 left-0 w-1 h-full cursor-ew-resize bg-transparent hover:bg-blue-500 opacity-0 hover:opacity-100 transition-opacity"
            onMouseDown={handleResizeMouseDown}
          />
        )}
      </aside>

      {/* 命令面板 */}
      {isCommandPaletteOpen && (
        <CommandPalette
          isOpen={isCommandPaletteOpen}
          commands={commands}
          onClose={() => setIsCommandPaletteOpen(false)}
        />
      )}

      {/* 折叠状态下的右侧展开按钮 */}
      {isRightSidebarCollapsed && (
        <button
          className="fixed right-4 bottom-4 bg-blue-600 text-white p-2 rounded-full shadow-lg hover:bg-blue-700"
          onClick={() => setIsRightSidebarCollapsed(false)}
          aria-label="Open AI Assistant"
        >
          <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M9.663 17h4.673M12 3v1m6.364 1.636l-.707.707M21 12h-1M4 12H3m3.343-5.657l-.707-.707m2.828 9.9a5 5 0 117.072 0l-.548.547A3.374 3.374 0 0014 18.469V19a2 2 0 11-4 0v-.531c0-.895-.356-1.754-.988-2.386l-.548-.547z"
            />
          </svg>
        </button>
      )}
    </div>
  );
};

export default WorkspacePage; 